import React from 'react'
import { Card, CardActions, CardContent, CardMedia, Button, Typography, ButtonBase } from '@material-ui/core'
import { ThumbUpAlt, ThumbUpAltOutlined } from '@material-ui/icons'
import DeleteIcon from '@material-ui/icons/Delete'
import MoreHorizIcon from '@material-ui/icons/MoreHoriz'
import moment from 'moment'
import useStyles from './styles';
import { useDispatch } from 'react-redux';

import { deletePost, likePost } from '../../../actions/posts'
import PostDetails from './PostDtl/PostDetails'
import { Link } from 'react-router-dom'

const Post = ({ post, setCurrentId }) => {

    const classes = useStyles();
    const dispatch = useDispatch()
    const user = JSON.parse(localStorage.getItem('profile'))

    const Likes = () => {
        if(post.likes.length > 0){
            return post.likes.find((like) => like === (user?.result?.sub || user?.result?._id))
                ? (
                    <><ThumbUpAlt fontSize='small'/>&nbsp;
                            { post.likes.length > 2 
                                ? `You and ${post.likes.length - 1} others` 
                                : `${post.likes.length} Like${post.likes.length > 1 ? 's' : ''} `
                            }
                    </>
                )
                : (
                    <><ThumbUpAltOutlined fontSize='small'/>&nbsp;{post.likes.length} {post.likes.length === 1 ? 'Like' : 'Likes'}</>
                )
        }
        return <><ThumbUpAltOutlined fontSize='small' />&nbsp;Like</>
    }

    const encodedID = window.btoa(post._id)
    return(
        
        <Card className={classes.card} raised elevation={6}>
            <Link  style={{ textDecoration: 'none'}}  component={Link} to={`/posts/${encodedID}`} > 
                <CardMedia className={classes.media} image={post.selectedFile} title={post.title} />
            </Link>
                <div className={classes.overlay}>
                    <Typography variant='h6'>{post.name}</Typography>
                    <Typography variant='body2'>{moment(post.createdAt).fromNow()}</Typography>
                </div>
                {(user?.result?.sub === post?.creator || user?.result?._id === post?.creator) && (
                <div className={classes.overlay2}>
                    <Button
                        style={{color: 'white'}}
                        size="small"
                        onClick={() => setCurrentId(post._id)}
                    >
                        <MoreHorizIcon fontSize='default' />
                    </Button>
                </div>
                )}
                <div className={classes.details}>
                    <Typography variant='body2' color="textSecondary">{post.tags.map((tag) =>  `#${tag} `)}</Typography>
                </div>
                <Typography className={classes.title} variant='h5' gutterBottom>{post.title}</Typography>
                <CardContent>
                    <Typography ariant='body2' color="textSecondary" component={'p'}>{post.message}</Typography>
                </CardContent>
            
            <CardActions className={classes.cardActions}>
                <Button size='small' color='primary' disabled={!user?.result} onClick={() => {
                    dispatch(likePost(post._id))
                    window.gtag("event", "post_clicked")
                    }}>                    
                <Likes />
                </Button>
                {(user?.result?.sub === post?.creator || user?.result?._id === post?.creator) && (
                    <Button size='small' color='warning' onClick={() => dispatch(deletePost(post._id))}>
                        <DeleteIcon fontSize='small' />&nbsp;Delete
                    </Button>
                )}
            </CardActions>            
        </Card>
        
    )
}

export default Post;